._ {
  display: flex;
  flex-direction: column;
  width: 10rem;
  height: 5.5rem;
  min-height: 2rem;
  border-radius: 0.5rem;
  padding: 1rem;
  background-color: var(--background-color-main);
  box-shadow: 0 0.5rem 1.5rem rgba(0, 0, 0, 0.1),
    0 0.25rem 0.5rem rgba(0, 0, 0, 0.1), 0 0.125rem 0.125rem rgba(0, 0, 0, 0.1);
}

.input {
  width: 8rem;
  height: 2rem;
  padding: 0.5rem;
  overflow: hidden;
  text-overflow: ellipsis;
}
